@import (less) "color.less";

.btn[disabled] {
  box-shadow: none !important;
  pointer-events: none;
  curson: not-allowed;
}

// text

.switch-button-text {
    text-align: center;
    position: relative;
    font-weight: bold;
    font-size: 0.750em;
    margin: 0 !important;
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1;
    outline: none !important;
    min-width: 9.584em;
    height: 4.167em;
    z-index: 0;
    &:active {
        box-shadow: inset 0.250em 0.250em 0.250em 0 rgba(0, 0, 0, 0.2);
    }
    .btn {
        border-radius: 0.125em !important;
    }
    
}
.switch-button-text.primary {
    color: @white;
    background-color: @green;
    border-color: @green;
    box-shadow: inset 0.250em 0.250em 0.250em 0 rgba(0, 0, 0, 0.2);
}
.switch-button-text.default {
    color: @gray;
    background-color: @white;
    border-color: @light_gray;
}
.switch-button-text[disabled] {
    box-shadow: none;
}
.switch-button-text.default[disabled] {
    &:hover {
        background-color: @white !important;
        border-color: @light_gray !important;
    }
}
.switch-button-text.primary[disabled] {
    &:hover {
        background-color: @green !important;
        border-color: @green !important;
    }
}

// Icon & Text

.switch-button-icontext {
    position: relative;
    font-weight: bold;
    font-size: 0.750em;
    margin: 0 !important;
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1;
    outline: none !important;
    min-width: 6.875em;
    height: 4.167em;
    z-index: 0;
    &:active {
        box-shadow: inset 0.250em 0.250em 0.250em 0 rgba(0, 0, 0, 0.2);
    }
    &.block-text-icon {
      padding-top: 0.416em;
    }
    &.inline-text-icon {
      padding-top: 1em;
    }

    .switch-label {
        padding-right: 0.625em;
    }
    .switch-label-icon {
        padding-bottom: 0.416em;
        display: table-cell !important;
        vertical-align: middle;
    }
    .switch-icon {
        font-size: 2.084em !important;
        display: table-cell !important;
    }
    .switch-icon-text-up {
        display: block !important;
        padding-bottom: 0.208em !important;
    }
    .btn {
        border-radius: 0.125em !important;
    }
   
}
.switch-button-icontext.primary {
    color: @white;
    background-color: @green;
    border-color: @green;
    box-shadow: inset 0.250em 0.250em 0.250em 0 rgba(0, 0, 0, 0.2);
}
.switch-button-icontext.default {
    color: @gray;
    background-color: @white;
    border-color: @light_gray;
}
.switch-button[disabled] {
    box-shadow: none;
}
.switch-button-icontext.default[disabled] {
    &:hover {
        background-color: @white !important;
        border-color: @light_gray !important;
    }
}
.switch-button-icontext.primary[disabled] {
    &:hover {
        background-color: @green !important;
        border-color: @green !important;
    }
}

// icon

.switch-button-icon {
    position: relative;
    font-weight: bold;
    font-size: 0.750em;
    margin: 0 !important;
    padding-top: 1em;
    padding-bottom: 1em;
    line-height: 1;
    outline: none !important;
    min-width: 6.250em;
    height: 4.167em;
    z-index: 0;
    &:active {
        box-shadow: inset 0.250em 0.250em 0.250em 0 rgba(0, 0, 0, 0.2);
    }

    .switch-icon {
        font-size: 2.084em !important;
    }
    .btn {
        border-radius: 2px !important;
    }

}
.switch-button-icon.primary {
    color: @white;
    background-color: @green;
    border-color: @green;
    box-shadow: inset 0.250em 0.250em 0.250em 0 rgba(0, 0, 0, 0.2);
}
.switch-button-icon.default {
    color: @gray;
    background-color: @white;
    border-color: @light_gray;
}
.switch-button[disabled] {
    box-shadow: none;
}
.switch-button-icon.default[disabled] {
    &:hover {
        background-color: @white !important;
        border-color: @light_gray !important;
    }
}
.switch-button-icon.primary[disabled] {
    &:hover {
        background-color: @green !important;
        border-color: @green !important;
    }
}

:host-context(.contrasto-on) {

    .switch-button-icontext.default,
    .switch-button-text.default,
    .switch-button-icon.default {
        color: @black;
        background-color: @white;
        border-color: @black;
    } 

    .switch-button-icontext.primary,
    .switch-button-text.primary,
    .switch-button-icon.primary,
    .switch-binary {
        color: @white;
        background-color: @black;
        border-color: @white;
    }

    .slider-box {
        background-color: @white;
        border-color: @black;
    }

    .switch-binary {
        .switch-label-icon-on {
            color: @white;
        }
        .switch-label-icon-off {
            color: @white;
        }
    }

    .switch-binary-true {
        background-color: @black;
    }
}